* {
    box-sizing: border-box;
}
.zooga {
    position: relative;
    width: 560px;
    height: 500px;
    background-color: rgb(255, 255, 255); 
}
.zooga-visor {
    margin-left: 60px;
}
.zooga-thumbs {
    position: absolute;
    top: 0;
    left: 0;
    padding: 4px
}
.zooga-thumb {
    cursor: pointer;
    width: 52px;
    height: 52px;
    margin-bottom: 5px;
    border: 1px solid #dddddd;
}
.zooga-thumb.active {
    border: 2px solid #006ef6;
}
.zooga-thumb img {
    height: 50px;
    width: 50px;
}
.zooga-thumb.active img {
    height: 48px;
    width: 48px;
}

* {
    box-sizing: border-box;
}
.zoo {
    position: relative;
}
.zoo-content {
    position: relative;
}
.zoo-visor {
    position: absolute;
    width: 500px;
    height: 500px;
    background: transparent;
    cursor: crosshair;
    z-index: 10; /* Esta por encima de todo */
 }

.zoo-lens {
    position: absolute;
    border: 1px solid #d4d4d4;
    background-color: rgba(255, 255, 255, .4);
    width: 260px;
    height: 260px;
    z-index: 1; /* Entre imagen y visor*/
}

.zoo-result {
    border: 1px solid #d4d4d4;
    width: 520px;
    height: 520px;
    position: absolute;
    top: 0;
    left: 505px;
    z-index: 1000;
}

